import React, {Component} from 'react';
import './Header.css';

// TEMP:
import profilePicture from './static/profilePicture.png';

import Projects from './Projects.js';
import Skills from './Skills.js';
import Footer from './Footer.js';
import NavBar from './NavBar.js';
import Blog from './Blog.js';
import Tooltip from './Tooltip.js';


class Social extends Component {
    render() {
        return (
            <a className="btn btn-social btn-hoverable" href={ this.props.s.l } rel="noopener noreferrer" target="_blank">
                <i className={`fab fa-${this.props.s.d}`}></i>
            </a>
        )
    }
}

class Header extends Component {
    constructor() {
        super();
        this.state = {
            nav: ['projects','skills','about'],
            component: "about"
        };
    }

    load = (c) => {
        this.setState({ component: c })
    }

    render() {
        const profile = this.props.profile;
        const socialKeys = Object.keys(profile.socials);
        const socials = socialKeys.map((social, i) => {
            // console.log(profile.socials[social]);
            return (<Social key={ i } s={ { d: social, l: profile.socials[social] } } />)
        });


        const projectIcon = <i className="fas mr-2 fa-project-diagram"></i>
        const blogIcon = <i className="fas mr-2 fa-rss"></i>
        const aboutIcon = <i className="far mr-2 fa-user-circle"></i>
        let displayComponent = <Projects />
        let active = 'btn-nav-active'
        let hoverable = 'btn-nav-hoverable'
        let projectsBtn = <li
            className={`list-group-item btn-nav no-select ${this.state.component==='projects' ? active : hoverable}`}
            onClick={() => this.load("projects")}>{ projectIcon } Projects</li>
        let blogBtn = <li
            className={`list-group-item btn-nav no-select ${this.state.component==='blog' ? active : hoverable}`}
            onClick={() => this.load("blog")}>{ blogIcon } Blog</li>
        let aboutBtn = <li
            className={`list-group-item btn-nav no-select ${this.state.component==='about' ? active : hoverable}`}
            onClick={() => this.load("about")}>{ aboutIcon } About</li>
        switch(this.state.component) {
            case "projects":
                displayComponent = <Projects />;
                break;
            case "blog":
                displayComponent = <Blog link={ this.props.profile.blog }/>;
                break;
            default:
                displayComponent = <Skills skills={ this.props.profile.skills }/>;
            }

        // Setup for mobile device NavBar
        const projectsNavBtn = <span onClick={() => this.load("projects")}>{ projectIcon }</span>
        const blogNavBtn = <span onClick={() => this.load("blog")}>{ blogIcon }</span>
        const aboutNavBtn = <span onClick={() => this.load("about")}>{ aboutIcon }</span>
        let navItems = [projectsNavBtn, blogNavBtn, aboutNavBtn]

        return (
            <div className="main row no-gutters">
                <NavBar navItems={navItems}/>
                <div className="col-md-3">
                    <div className="left">
                        <div className="center-wrapper">
                            <div className="center-content">
                                <h1 className="profile-name">{ profile.name }</h1>

                                <img src={ profilePicture } className="profile-picture" alt="">
                                </img>

                                <div className="socials">
                                    <div className="">
                                        { socials }
                                    </div>
                                    <a class="btn btn-resume mb-1" href={`mailto:${ profile.email }`}>
                                        <i class="fas fa-envelope mr-2"></i>
                                        Email for Resume.
                                    </a>
                                </div>

                                <ul className="list-group list-group-flush">
                                    { projectsBtn }
                                    { blogBtn }
                                    { aboutBtn }
                                    {/*
                                    {this.state.nav.map(function(name, index){
                                        return (
                                            <li
                                                class="list-group-item btn-nav"
                                                onClick={ () => this.load(name) }
                                                key={ index }>
                                                { name }
                                            </li>
                                            );
                                    })}
                                    */}
                                </ul>

                                <Footer email={ profile.socials.google }/>

                                <Tooltip html={ <i class="fab fa-react"></i> } content="Powered with React"/>
                                <Tooltip html={ <i class="fab fa-bootstrap"></i> } content="Styled with Bootstrap"/>
                                <Tooltip html={ <i class="fab fa-font-awesome-flag"></i> } content="Icons from Fontawesome"/>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-9">
                    { displayComponent }
                </div>
            </div>
        );
    }
}

export default Header;
