import React, {Component} from 'react';
import './NavBar.css';

class NavBar extends Component {
    render() {
        console.log(this.props.navItems);
        const navItems = this.props.navItems.map((nav, i) => {
            return (
                <span className="nav-item" key={ i }>
                    { nav }
                </span>)
        })
        return (
            <nav className="navbar">
                <span className="navbar-brand mb-0 h1">Peter</span>

                <div className="center-wrapper">
                    { navItems }
                </div>
            </nav>
        );
    }
}

export default NavBar;
