import React, {Component} from 'react';
import './Project.css';

import ProjectTags from './ProjectTags.js';
import ProjectLanguages from './ProjectLanguages.js';

function FadeInSection(props) {
    const [isVisible, setVisible] = React.useState(false);  // React Hooks: NEED TO LEARN
    const domRef = React.useRef();
    React.useEffect(() => {
        const observer = new IntersectionObserver(entries => {
            entries.forEach(entry => setVisible(entry.isIntersecting));
        });
        observer.observe(domRef.current);
    }, []);
    return (
        <div
          className={`fade-in-section ${isVisible ? 'is-visible' : ''}`}
          ref={domRef} >
          {props.children}
        </div>
    );
}

class Project extends Component {
    render() {
        return (
            <div className="col-lg-6 border-0">
            <FadeInSection>
                <div className="section">
                    <div className="project-preview" style= { { 'backgroundColor': this.props.project.colour } }>
                        <img className="project-image" src={ this.props.project.image } alt="" />
                    </div>
                    <div className="project-body container">
                        <a href={this.props.project.links.github} target="_blank" rel="noopener noreferrer">
                            <i class="fab float-right fa-github project-repo"></i>
                        </a>
                        <a className="project-title" href={ `${this.props.project.links.site}` } target="_blank" rel="noopener noreferrer">{ this.props.project.projectName }</a>
                        <p className="project-description">{ this.props.project.type }</p>
                        <p className="">{ this.props.project.description }</p>

                        <div className="row no-gutters">
                            <div className="col-sm-4">
                                <p className="project-meta-data">
                                    <i className="meta-icon fas fa-calendar-day"></i>
                                    { this.props.project.date }
                                </p>
                            </div>

                            <div className="col-sm-4">
                            <p className="project-meta-data">
                                <i className={ `meta-icon fas fa-toggle-${this.props.project.discontinue ? 'off' : 'on'}` }></i>
                                { this.props.project.discontinue ? 'DISCONTINUED' : 'MAINTAINED' }
                            </p>
                            </div>

                            <div className="col-sm-4">
                                <ProjectLanguages languages={ this.props.project.languages } />
                            </div>
                        </div>

                        <ProjectTags tags={ this.props.project.tags } />
                    </div>

                </div>
            </FadeInSection>
            </div>
        );
    }
}

export default Project;
