import React, {Component} from 'react';
import './Tooltip.css';

class Tooltip extends Component {
    render() {
        return (
            <span className="mr-3">
                <div className="toooltip">
                    { this.props.html }
                    <span class="tooltiptext">{ this.props.content }</span>
                </div>
            </span>
        );
    }
}

export default Tooltip;
