import React, {Component} from 'react';
import './Skills.css';

import Emoji from './Emoji.js';

function Fill(props) {
    const [isVisible, setVisible] = React.useState(false);  // React Hooks: NEED TO LEARN
    const domRef = React.useRef();
    React.useEffect(() => {
        const observer = new IntersectionObserver(entries => {
            entries.forEach(entry => setVisible(entry.isIntersecting));
        });
        observer.observe(domRef.current);
    }, []);
    return (
        <div
          className={`slide-in-bar ${isVisible ? 'side-animate' : ''}`}
          ref={domRef} >
          {props.children}
        </div>
    );
}

class Skills extends Component {
    render() {
        // Technical Skills
        const technical = this.props.skills.technical
        const technicalKeys = Object.keys(technical);

        const technicalSkills = technicalKeys.map(skill => {
            return (
                    <SkillBar key={ skill }
                        skillLevel={ technical[skill] }
                        skill={ skill }
                    />
                )
        });

        // Generals Skills
        const general = this.props.skills.general
        const generalKeys = Object.keys(general);

        const generalSkills = generalKeys.map(skill => {
            return (
                    <SkillBar key={ skill }
                        skillLevel={ general[skill] }
                        skill={ skill }
                    />
            )
        });

        const languages = this.props.skills.languages
        // const languages = Object.keys(langauge);
        const otherLanguages = []
        const languagesSkills = [];
        for (var language in languages) {
            if (languages.hasOwnProperty(language)) {
                if(parseInt(languages[language], 10) <= 50) {
                    otherLanguages.push(<li key={ language }>
                        { language }
                    </li>)
                } else {
                    languagesSkills.push(
                        <SkillBar key={ language }
                            skillLevel={ languages[language] }
                            skill={ language }
                        />)
                }
                // console.log(key, langauge[key]);
            }
        }
        // const langaugeSkills = langaugeKey.map(skill => {
        //     return (
        //             <SkillBar key = { skill }
        //                 skillLevel = { langauge[skill] }
        //                 skill = { skill }
        //             />
        //     )
        // });

        return (
            <div className="scrollable-content">
                <h2 className="scrollable-content-header text-center mb-5">About</h2>

                <div className="section section-about">
                    Hello <Emoji key={ "waving-hand-sign" } emoji="üëã" label="waving-hand-sign"/>,
                    My name is Peter Tran, and I'm currently studying
                    Biomedical Engineering/Software Engineering (Masters) at UNSW.
                </div>

                <div className="section section-about">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="skill-header">Technical Skills</div>
                            { technicalSkills }
                        </div>

                        <div className="col-md-6">
                            <div className="skill-header">General Skills</div>
                            { generalSkills }
                        </div>
                    </div>
                </div>

                <div className="section section-about">
                    <h2 className="text-center mb-5"><i className="fas fa-code"></i> Programming Experience</h2>
                    <div className="row">
                        <div className="col-md-6">
                            { languagesSkills }
                        </div>

                        <div className="col-md-6">
                            <p>
                                Coded in these languages in the past
                            </p>
                            <ul>
                                { otherLanguages }
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

function SkillBar(props) {
    return (
        <div>
            <div className="skill-bar">
            <Fill>
                <div className="bar" style={ { width: props.skillLevel } }>{ props.skill }</div>
            </Fill>
            </div>
        </div>
    )
}

export default Skills;
